import {
  reactive,
  toRefs
} from 'vue'
import { useTabsInfoState } from './state'

const useMethod = (state, props) => {
  const scrollInto = (index) => {
    const name = state.tabsList[index].viewInto
    props.scrollInTo(name)
  }

  return {
    scrollInto
  }
}

const useTabs = (props, context) => {
  const state = reactive(useTabsInfoState)
  const { scrollInto } = useMethod(state, props, context)

  return {
    ...toRefs(state),
    scrollInto
  }
}

export default useTabs